<template>
  <!--<div> -->


 <md-layout :md-gutter="8"> 
  <!-- left navigation  -->
  <md-layout md-column md-flex="30" v-show="!hideMenu" > 
    <!--<md-toolbar  md-theme="white">-->
     <md-toolbar> 
      <div class="md-toolbar">
         <h2 class="md-title" style="flex: 1">Categories</h2>
       
         <md-button @click.native='getCategories()' class="md-fab md-mini md-clean">
           <md-icon>refresh</md-icon>
        </md-button>
        <!--  Testing buttons      
          <button @click='postCategory()'>Post</button>
           <button @click='getCategories()'>GET</button>  
        -->
      </div>
    </md-toolbar>
    <!-- Categories -->
    <md-list class="md-dense md-theme-green" v-for="(category, catkey) in categories">
     <!--<md-whiteframe md-elevation="3"> -->
     <div  @click="selectCategory(category, catkey)">  
     <md-list-item ref='category'>
      <div>{{category.name}}</div>
        <md-list-expand>
          <!-- Topics in Category -->
          <md-list  v-for="(topic, topkey) in category.topics">
            <md-list-item @click.native='setTopicCards(topic)' ref='topic' class="md-inset">{{topic.name}}</md-list-item>
          </md-list>
        </md-list-expand>
        <md-divider class="md-inset"></md-divider>
     </md-list-item>
     </div>
     <!--</md-whiteframe>-->
   </md-list>
   <!-- speed dial button at bottom of navigation menu --> 
   <md-speed-dial md-mode="scale" class="md-fab-bottom-left">
    <md-button class="md-fab md-primary" md-fab-trigger>
      <md-icon md-icon-morph>close</md-icon>
      <md-icon>add</md-icon>
    </md-button>
    <md-button @click.native='addTopic()' class="md-fab md-mini md-clean">
      <md-icon>subject</md-icon>
    </md-button>
    <md-button @click.native='addCategory()' class="md-fab md-mini md-clean">
      <md-icon>toc</md-icon>
    </md-button>
   </md-speed-dial>
  </md-layout>  <!-- END left navigation  -->

  <!-- main display  --> 
  <md-layout md-column :md-gutter="8">
   
   <md-toolbar>
     <md-button-toggle class="md-warn" > 
      <md-button class="md-icon-button md-toggle" @click.native='hideMenu = !hideMenu'>
         <md-tooltip md-direction="bottom">Toggle menu display</md-tooltip>
        <md-icon>menu</md-icon>
      </md-button>
    </md-button-toggle>
     
      <h2>{{topic.description}}</h2>

   </md-toolbar>  


   MAIN SECTION
   <md-layout :md-gutter="8">
     <!-- code cards -->  
     <md-layout md-flex='100' md-flex-xsmall='100' md-flex-small='100' md-flex-medium='100' md-flex-large='50' md-flex-xlarge='50' v-for="card in cards">
       <codecard :card="card">  </codecard>
     </md-layout>
   </md-layout>

  </md-layout> <!-- end main section -->

  <!-- Buttons at bottom of page -->
  <md-button class="md-fab md-fab-bottom-center" disabled>
    <md-icon>save</md-icon>
  </md-button>
  
  <md-speed-dial md-mode="scale" class="md-fab-bottom-right">
  <md-button class="md-fab" md-fab-trigger>
    <md-icon md-icon-morph>close</md-icon>
    <md-icon>edit</md-icon>
  </md-button>

  <md-button class="md-fab md-mini md-clean">
    <md-icon>save</md-icon>
  </md-button>

  <md-button class="md-fab md-mini md-clean">
    <md-icon>content_copy</md-icon>
  </md-button>
  </md-speed-dial>


</template>

<script>
import {eventBus} from '../global.js'
import codecard from 'components/Codecard.vue'
import Vue from 'vue'

export default {
  components: {
    codecard: codecard
  },
  created () {
    var _this = this
    eventBus.$on('hide-message', function () {
      // _this.showHideMessage()
    })
    eventBus.$on('save-card', function (card) {
      _this.updateCard(card)
    })
  },
  data () {
    return {
      data: {},
      dataArray: [],
      categories: [],
      category: [], // selected category
      catkey: [], // selected category key
      topics: [],
      topic: [],
      cardsAll: [],  // all cards
      cards: [], // current topic cards
      card: [],  // selected card
      hideMenu: false // left menu display
    }
  },
  methods: {
    selectCategory (category, catkey) {
      this.category = category
      this.catkey = catkey
      console.log('select category: ', this.category)
      console.log('catkey', this.catkey)
    },
    addTopic () {
      console.log('add Topic to category...', this.category)
      console.log('add Topic with catkey: ', this.catkey)
    },
    addCategory () {
      console.log('add Category...')
    },
    updateCard (card) {
      // var catkey = this.category.key
      // var topkey = this.topic.key
      var cardkey = card.key
      console.log('UpdateCard...')
      // this.$http.post('https://vue-helper.firebaseio.com/categories/' + catkey + '/topics/' + topkey + ' /codecard/' + cardkey + '.json', card)
      this.$http.patch('https://vue-helper.firebaseio.com/codecards/' + cardkey + '.json', card)
    },
    postCategory () {
      var newCategory = {
        name: 'Category test post',
        description: 'Category test post description'
      }
      this.$http.post('https://vue-helper.firebaseio.com/categories.json', newCategory)
      .then(function (response) {
        console.log(response)
        var key
        key = response.body.name
        newCategory.key = key
        // use Vue.set to add new item reactively
        Vue.set(this.categories, key, newCategory)
        console.log('new categories: ', this.categories)
      })
     .catch(function (error) {
       console.log(error)
     })
    },
    getCategories () {
      // const resultArray = []
      this.$http.get('https://vue-helper.firebaseio.com/categories.json')
      .then(response => {
        this.categories = response.body
        var key, k1
        for (key in this.categories) {
          this.categories[key].key = key
          for (k1 in this.categories[key].topics) {
            this.categories[key].topics[k1].key = k1
          }
        }
        // console.log('Object.keys:', Object.keys(this.categories))
        // console.log('this.categories ', this.categories)
        this.getCardsAll()
        return this.data
      })
      .catch(function (error) {
        console.log('error: ', error)
      })
    },
    getCardsAll () {
      // const resultArray = []
      console.log('getCardsAll... ')
      this.$http.get('https://vue-helper.firebaseio.com/codecards.json')
      .then(response => {
        this.cardsAll = response.body
        var key
        for (key in this.cardsAll) {
          this.cardsAll[key].key = key
        }
        // console.log('this.cardsAll ', this.cardsAll)
        return this.data
      })
      .catch(function (error) {
        console.log('error: ', error)
      })
    },
    getTest () {
      // const resultArray = []
      // this.$http.get('https://vue-helper.firebaseio.com/categories.json')
      // this.$http.get('https://vue-helper.firebaseio.com/categories/-KcoZM5W9IVbczugSnQR/topics/-Kcoa0a0QQfc49twyszh/codecard.json')
      this.$http.get('https://vue-helper.firebaseio.com/categories.json')
      .then(response => {
        this.categories = response.body
        var key, i, j
        for (key in this.categories) {
          this.categories[key].key = key
          for (i in this.categories[key].topics) {
            this.categories[key].topics[i].key = i
            for (j in this.categories[key].topics[i].codecard) {
              this.categories[key].topics[i].codecard[j].key = j
            }
          }
        }
        console.log('Object.keys:', Object.keys(this.categories))
        console.log('this.categories ', this.categories)

        // this.cards = this.data
        // console.log('cards: ', this.cards)
        return this.data
      })
      .catch(function (error) {
        console.log('error: ', error)
      })
    },
    setTopicCards (topic) {
      console.log('topic.key', topic.key)
      console.log('$refs.category ', this.$refs.category)
      this.cards = []
      this.topic = topic
      var key
      console.log('key in this.cardsAll...')
      // set this.cards to cards for selected topic
      for (key in this.cardsAll) {
        console.log('topicID: ', this.cardsAll[key].topicID)
        if (this.cardsAll[key].topicID === topic.key) {
          this.cards.push(this.cardsAll[key])
          // console.log('push', this.cards)
        }
      }
      // console.log('object keys(this.cards) ', Object.keys(this.cards))
      // console.log('current topic: ', this.topic)
      console.log('current cards: ', this.cards)
    },
    toggleLeftSidenav () {
      this.$refs.leftSidenav.toggle()
    },
    toggleRightSidenav () {
      this.$refs.rightSidenav.toggle()
    },
    closeRightSidenav () {
      this.$refs.rightSidenav.close()
    },
    open (ref) {
      console.log('Opened: ' + ref)
    },
    close (ref) {
      console.log('Closed: ' + ref)
    }
  }
}
</script>

<style>
</style>



