<template>
  <div style="width:100%">

<div class="wrapper">
  <div class="flip-container">
    <div class="flipper"  v-bind:class="{clicked: isBack}">
      <div class="front">
      
        <md-card class="box-card">
        <md-card-header>
     
          <md-toolbar v-bind:class="[{'md-primary': isJS() },{'html': isHTML() }]" >
            <h2 class="md-title" style="flex: 1">
             <md-input-container>
               <md-input class="md-title" v-model="card.title" placeholder="title">
                 
               </md-input>
             </md-input-container> 
              </h2>    
         
             <md-button  @click.native='rotateCard()'>
                <md-icon>flip_to_back</md-icon>
              </md-button>
             <!--  <md-button  @click.native='jscode=formatJSCode(jscode)'>
                <md-icon>format_line_spacing</md-icon>
              </md-button>-->
              <md-button v-clipboard="jscodecomputed" class="md-fab md-clean md-mini ">
                <md-tooltip md-direction="right">Copy code</md-tooltip>
                <md-icon>content_copy</md-icon>
              </md-button>
          </md-toolbar>
        </md-card-header>
        <md-card-content >
            <codemirror v-model="card.code" :options="editorOption">
            </codemirror>
        </md-card-content>
        <md-card-actions>
          <md-button class="md-fab md-clean md-mini" @click.native='saveCard()'>
                <md-tooltip md-direction="bottom">Save changes</md-tooltip>
            <md-icon>save</md-icon>
          </md-button>
         </md-card-actions>  
      </md-card>
    </div><!-- front -->

    <div class="back">
        <md-card class="box-card">
          <md-card-header>
          
          <md-toolbar style="background-color: lightslategrey">
            <h2 class="md-title" style="flex: 1"> 
            <md-input-container>
            <md-input class="md-title" v-model="card.backtitle" placeholder="back title">
             
            </md-input>          
            </md-input-container>
            </h2>
              <md-button  @click.native='rotateCard()'>
                <md-icon>flip_to_front</md-icon>
              </md-button>
              <md-button v-clipboard="card.code" class="md-fab md-clean md-mini ">
                <md-tooltip md-direction="bottom">Copy code</md-tooltip>
                <md-icon>content_copy</md-icon>
              </md-button>
            </md-toolbar>
        </md-card-header>
        <md-card-content>
          <md-input-container>
              <label>Instructions</label>
              <md-textarea rows="7" v-model="card.backtext"></md-textarea>
           </md-input-container>
        </md-card-content>
         <md-card-actions>
         <!--  !!!! problem code
          <md-speed-dial md-mode="scale" class="md-fab-bottom-right">
             <md-button class="md-fab md-clean md-mini" @click.native='saveCard()'>
               <md-tooltip md-direction="bottom">Save changes</md-tooltip>
                <md-icon>save</md-icon>
           </md-button>
            </md-speed-dial>-->
       
         </md-card-actions>  
      </md-card>
      </div><!-- back -->
    </div><!-- flipper -->
  </div><!-- flip-container -->
</div><!-- wrapper -->

</div>
</template>

<script>
import {eventBus} from '../global.js'
import beautifyJS from 'js-beautify'
var beautifyHtml = require('js-beautify').html
// var beautifCss = require('js-beautify').css;

export default {
  // componenents: {compname: Compname},
  name: 'codecard',
  created: function () {
    // created
    // this.jscode = this.formatCode(this.card)
    this.card.code = this.formatCode(this.card)
    // this.htmlcode = this.formatHTMLCode(this.htmlcode)
  },
  props: ['card', 'cardKey'],
  mounted () {
    // console.log('mounted')
    // this.jscode = this.card.codePrefix + this.formatCode(this.card.code) + this.card.codeSuffix
  },
  updated () {
    // console.log('updated')
  },
  computed: {
    currentUser () {
      // console.log('computed username')
    },
    jscodecomputed () {
      return this.jscode
    }
  },
  data: function () {
    return {
      jscode: '',
      htmlcode: '<div class="page-content">\n<md-layout :md-gutter="8"><md-layout md-flex="5"></md-layout><md-layout></md-layout><md-layout md-flex="5"></md-layout></md-layout>\n</div>',
      cardchecked: false,
      cardData: [],
      isBack: false,
      dataLoading: false,
      editorOption: {
        tabSize: 2,
        mode: 'text/javascript', // text/javascript, text/x-vue
        theme: 'night', // panda-syntax, night,seti,xq-dark neat elegant', 'rubyblue', 'monokai'
        lineNumbers: false,
        line: true,
        keyMap: 'sublime',
        extraKeys: { 'Ctrl': 'autocomplete' },
        foldGutter: true,
        // height: 100,
        gutters: ['CodeMirror-linenumbers', 'CodeMirror-foldgutter'],
        styleSelectedText: true,
        highlightSelectionMatches: { showToken: /\w/, annotateScrollbar: true }
        // viewportMargin: infinity
      },
      htmlOption: {
        tabSize: 2,
        mode: 'text/x-vue', // text/javascript, text/x-vue
        theme: 'neat', // panda-syntax, night,seti,xq-dark neat elegant', 'rubyblue', 'monokai'
        lineNumbers: false,
        line: true,
        keyMap: 'sublime',
        extraKeys: { 'Ctrl': 'autocomplete' },
        foldGutter: true,
        // height: 100,
        gutters: ['CodeMirror-linenumbers', 'CodeMirror-foldgutter'],
        styleSelectedText: true,
        highlightSelectionMatches: { showToken: /\w/, annotateScrollbar: true }
        // viewportMargin: infinity
      }
    }
  },
  methods: {
    isJS () {
      if (this.card.type === 'JS') {
        return true
      } else {
        return false
      }
    },
    isHTML () {
      if (this.card.type === 'HTML') {
        return true
      } else {
        return false
      }
    },
    setHidden () {
      eventBus.$emit('hide-message')
    },
    saveCard () {
      eventBus.$emit('save-card', this.card)
      console.log('$emit(save-card)', this.card)
    },
    rotateCard () {
      this.isBack = !this.isBack
    },
    formatCode (card) {
      if (card.type === 'JS') {
        return this.formatJSCode(card.code)
      }
      if (card.type === 'HTML') {
        return this.formatHTMLCode(card.code)
      }
    },
    formatJSCode (code) {
      return beautifyJS(code, {
        'indent_size': 2,
        'indent_char': ' ',
        'eol': '\n',
        'end_with_newline': true,
        'other': ' ',
        'indent_level': 0,
        'indent_with_tabs': false,
        'preserve_newlines': true,
        'max_preserve_newlines': 2,
        'jslint_happy': true,
        'indent_handlebars': true,
        'object': {}
      })
    },
    formatHTMLCode (code) {
      return beautifyHtml(code, {
        'indent_size': 2,
        'indent_char': ' ',
        'eol': '\n',
        'end_with_newline': true,
        'other': ' ',
        'indent_level': 0,
        'indent_with_tabs': false,
        'preserve_newlines': true,
        'max_preserve_newlines': 2,
        'jslint_happy': true,
        'indent_handlebars': true,
        'object': {}
      })
    }
  },
  destroyed: function () {
    //  code here
  }
}
</script>

<style scoped>
 .js {
   background: blue;
 }
 .html {
   background: green !important;
 }
 .CodeMirror {
   height: 200px !important;
 }
 .CodeMirror-code {
   line-height: 1.2em !important;
   /*font-size: 12px !important;*/
 }
 .box-title {
  /*background-color: blue;*/
    color: white;
  }
  .box-card {
   /* width: 480px; */
   /* width: 100%; */
    height: 375px;
    text-overflow: ellipsis;
    /*overflow: scroll;*/
  }
.flip-container {
  perspective: 1000;
}

/*.flip-container:hover .flipper {
  transform: rotateY(180deg);
}*/
.flipper.clicked {
  transform: rotateY(180deg);
}
.flip-container, 
.front, 
.back {
    /*width: 300px;*/
    height: 420px;
    width: 100%;
}

.flipper {
    transition: 0.6s;
    transform-style: preserve-3d;
    position: relative;
}

.front, 
.back {
    backface-visibility: hidden;
    position: absolute;
    top: 0;
    left: 0;
}

.front {
  z-index: 2;
}

.back {
  transform: rotateY(180deg);
}

</style>



